.battery-section {
  margin: 0;

  position: relative;
  height: 550px;
  padding: 50px;
  text-align: center;

  background: #f4cc70;
  background: -webkit-linear-gradient(top, #f4cc70 0%,#f4cc70 61%,#dd985a 100%);
  background: -webkit-gradient(linear, left top, left bottom, from(#f4cc70),color-stop(61%, #f4cc70),to(#dd985a));
  background: -o-linear-gradient(top, #f4cc70 0%,#f4cc70 61%,#dd985a 100%);
  background: linear-gradient(to bottom, #f4cc70 0%,#f4cc70 61%,#dd985a 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f4cc70', endColorstr='#dd985a',GradientType=0 );
}

.battery-section h1 {
  font-weight: 500;
  margin: 10px 0;
  color: #000;
  font-size: 50px;
}

.battery-section h3 {
  font-size: 25px;
  margin: 10px 0;
  font-weight: 100;
}

.battery-section p {
  font-size: 15px;
  margin: 10px 0;
}

.battery-section div {
  text-align: center;
}

.CircularProgressbar {
  max-width: 90%;
  margin-top: 90px;
}



