.bot-section {
    position: absolute;
    z-index: 10;
    margin-left: 15px;
    margin-top: -145px;
  }
  
.speech-cloud {
    margin: 0 30px 0 0;
    background-color: #fff;
    padding: 20px;
    width: 220px;
    height: 90px;
    display: inline-block;
    vertical-align: bottom;
}
  
