.bot-section p {
    font-size: 13px;
    letter-spacing: 1px;
    line-height: 19px;
    margin: 0;
}

.bot-section img {
    width: auto;
    height: 80px;
    right: 15px;
    top:0 ;
    position: relative;
}

.Cursor.Cursor--blinking {
    display: none;
}

.next-btn {
    font-weight: 900;
    color: #000;
    top: 0px;
    text-decoration: none;
    font-size: 12px !important;
    position: absolute;
    right: 15px;
    background: #B8D7CD;
    padding: 5px;
}

.bot-section::before {
    background-color: rgba(0,0,0,.4);
    content: '';
    width: 370px;
    height: 650px;
    position: absolute;
    top: -505px;
    bottom: 0;
    left: -15px;
    right: 0;
    z-index: -1;
}

.bonus {
    color: #a2c523;
}


