.session-section {
    margin: 0;

    position: relative;
    background: #f75418; /* Old browsers */ /* FF3.6-15 */
    background: -webkit-linear-gradient(top, #90afc5 0%,#336b87 100%); /* Chrome10-25,Safari5.1-6 */
    background: -webkit-gradient(linear, left top, left bottom, from(#90afc5),to(#336b87));
    background: -o-linear-gradient(top, #90afc5 0%,#336b87 100%);
    background: linear-gradient(to bottom, #90afc5 0%,#336b87 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#90afc5', endColorstr='#336b87',GradientType=0 ); /* IE6-9 */
    height: 550px;
    padding: 50px;
    text-align: center;
}
  
.session-section h1 {
    font-weight: 500;
    margin: 10px 0;
    color: #fff;
    font-size: 50px;
}

.saved-sessions p {
    color: #fff;
    margin: 0;
    padding-left: 50px;
  }
  
  .session-section .saved-sessions {
    position: relative;
    top: 115px;
  }
  
  .session-section form {
    margin: 0;
    text-align: center;
  }
  
  .saved-sessions p.sessions:before {
    content: '\2713';
    position: absolute;
    width: 30px;
    height: 45px;
    top: 0;
    left: 0;
    background: #fff;
    color: #20C476;
    padding: 10px;
    font-size: 30px;
    text-align: center;
    line-height: 48px;
  }

  .session-section form button,
  .back-btn {
    background-color: #fff;
    padding: 15px 25px;
    border-radius: 25px;
    text-decoration: none;
    color: #000;
    position: relative;
    top: 30px;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
    cursor: pointer;
    font-size: 16px;
    border: none;
    margin-bottom: 35px;
}

.session-section form button:hover,
.back-btn:hover {
    background-color: #000;
    color: #fff
}
  
  .session-section form input[type='time'] {
    display: block;
    margin: 30px auto;
    padding: 20px;
    font-size: 20px;
    text-align: center;
    border-radius: 4px;
    border: none;
  }

  .saved-sessions .Toastify__toast-container--top-right {
    top: -550px !important;
    left: -50px;
  }
  
  .saved-sessions .Toastify__toast-container--top-right .Toastify__toast--default {
    background-color: rgb(161, 5, 5) !important;
  }

  .saved-sessions .Toastify__toast-body p {
    padding-left: 0;
  }
  


