.maps-section {
    height: 550px;
    margin: 0;
    padding: 50px;
    position: relative;
    background: #90afc5; /* Old browsers */ /* FF3.6-15 */
    background: -webkit-linear-gradient(top, #90afc5 39%,#336b87 100%); /* Chrome10-25,Safari5.1-6 */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(39%, #90afc5),to(#336b87));
    background: -o-linear-gradient(top, #90afc5 39%,#336b87 100%);
    background: linear-gradient(to bottom, #90afc5 39%,#336b87 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#90afc5', endColorstr='#336b87',GradientType=0 );
}

.maps-section img {
    height: auto;
    margin: 0;
    width: 330px;
    border-radius: 10px;
}

.maps-section h1 {
    font-weight: 500;
    margin: 10px 0 30px;
    color: #fff;
    font-size: 35px;
}


