.smart-section {
    margin: 0;

    position: relative;
    background: #87b3b5; /* Old browsers */ /* FF3.6-15 */
    background: -webkit-linear-gradient(top, #87b3b5 0%,#87b3b5 49%,#20948b 100%); /* Chrome10-25,Safari5.1-6 */
    background: -webkit-gradient(linear, left top, left bottom, from(#87b3b5),color-stop(49%, #87b3b5),to(#20948b));
    background: -o-linear-gradient(top, #87b3b5 0%,#87b3b5 49%,#20948b 100%);
    background: linear-gradient(to bottom, #87b3b5 0%,#87b3b5 49%,#20948b 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#87b3b5', endColorstr='#20948b',GradientType=0 ); /* IE6-9 */
    height: 550px;
    padding: 50px;
    text-align: center;
}
  
.smart-section h1 {
    font-weight: 500;
    margin: 10px 0;
    color: #fff;
    font-size: 50px;
}
  
.smart-section h3 {
    font-size: 25px;
    margin: 10px 0;
    font-weight: 100;
    color: #fff;
}

.smart-section h3:last-of-type {
    font-size: 50px;
    font-weight: 800;
}
  
.smart-section p {
    font-size: 15px;
    margin: 10px 0;
} 
  
.smart-section div {
    text-align: center;
}

.session-btn {
    background-color: #fff;
    padding: 15px 25px;
    border-radius: 25px;
    text-decoration: none;
    color: #000;
    position: relative;
    top: 30px;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
}

.session-btn:hover {
    background-color: #000;
    color: #fff
}

.smart-section img {
    margin: 100px auto 0 auto;
    width: 200px;
}

.shop-btn {
    background-color: #fff;
    padding: 10px 20px;
    border-radius: 25px;
    text-decoration: none;
    color: #000;
    position: relative;
    top: 35px;
    font-size:15px;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
}


