.usage-section {
    margin: 0;

    position: relative;
    background: #d09683; /* Old browsers */ /* FF3.6-15 */
    background: -webkit-linear-gradient(top, #d09683 47%,#d09683 47%,#73605b 100%); /* Chrome10-25,Safari5.1-6 */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(47%, #d09683),color-stop(47%, #d09683),to(#73605b));
    background: -o-linear-gradient(top, #d09683 47%,#d09683 47%,#73605b 100%);
    background: linear-gradient(to bottom, #d09683 47%,#d09683 47%,#73605b 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#d09683', endColorstr='#73605b',GradientType=0 ); /* IE6-9 */
    height: 640px;
    padding: 5px 0;
    text-align: center;
}

.usage-block { 
    background-color: #fff;
    margin: 30px;
    padding: 50px;
    height: 480px;
    border-radius: 10px;
}
  
.usage-section h1 {
    font-weight: 500;
    margin: 0;
    padding-top: 50px;
    color: #fff;
    font-size: 50px;
}
  
.usage-section h3 {
    font-size: 20px;
    margin: 10px 0;
    font-weight: 100;
    color: #fff;
}
  
.usage-section p {
    font-size: 15px;
    margin: 10px 0;
} 
  
.usage-section div {
    text-align: left;
    margin-left: 10px;
    color: #000;
}

.usage-btn {
    background-color: #fff;
    padding: 15px 25px;
    border-radius: 25px;
    text-decoration: none;
    color: #000;
    position: relative;
    top: 30px;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
}

.usage-btn:hover {
    background-color: #000;
    color: #fff
}

.usage-block table {
    width: 310px;
    position: relative;
    left: -50px;
    border-radius: 10px;
    margin-top: 30px;
}

.usage-block table tr th,
.usage-block table tr td {
    padding: 10px;
}

.usage-block table tr td {
    background-color: #eee;
    border: 9px solid #fff;
    border-radius: 15px;
}

.back-btn {
    background-color: #fff;
    padding: 15px 25px;
    border-radius: 25px;
    text-decoration: none;
    color: #000;
    position: relative;
    top: 30px;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
}

.session-btn:hover {
    background-color: #000;
    color: #fff
}

.usage-section .slick-arrow {
    display: none !important;
}

.usage-section .slick-initialized .slick-slide {
    margin-right: 30px;
    width: 230px !important;
    height: auto;
    background-color: #fff;
    padding: 20px 10px;
    border-radius: 5px;
    -webkit-box-shadow: 5px 5px 35px rgba(0,0,0,.5);
            box-shadow: 5px 5px 35px rgba(0,0,0,.5);
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}

.usage-section .slick-initialized .slick-slide:hover {
    background-color: #eee;
    -webkit-transform: translateY(5px); 
      -ms-transform: translateY(5px); 
      transform: translateY(5px); 
}

.usage-section .slick-initialized {
    position: relative;
    margin: 60px 0 0 0;
}

.usage-section .slick-list {
    height: 290px;
}


