body {
  margin: 0 auto;
  padding: 0;
  font-family: sans-serif;
}

.home-section {
  background-color: #ebebeb;
  width: 100%;
  max-width: 370px;
  margin: 40px auto;
  border: 1px solid #eee;
}

.reward-contents {
  background: #fff;
  margin: 30px;
  padding: 40px;
  text-align: center;
  position: relative;
  top: 10%;
}

.reward-contents p b {
  font-size: 30px;
  margin-bottom: 10px;
}

.reward-contents p {
  font-size: 18px;
  margin-bottom:30px;
}

.slick-next:before {
  content: 'Volgende' !important;
  font-family: 'Arial' !important;
  font-size: 15px !important;
}

.slick-prev:before {
  content: 'Vorige' !important;
  font-family: 'Arial' !important;
  font-size: 15px !important;
}

.slick-prev, .slick-next {
  top: auto !important;
  bottom: 10px;
  width: auto !important;
  height: auto !important;
  padding: 10px 40px !important;
}

.slick-next {
  right: 0 !important;
}

.slick-prev {
  left: 0 !important;
  z-index: 1;
}

.highlight {
  position: absolute;
  background: rgba(0,0,0,.8);
  width: 370px;
  height: 695px;
  top: 40px;
  bottom: auto;
  left: auto;
  right: auto;
}

.Toastify__toast-container--top-right {
  top: auto !important;
  right: auto !important;
  left: auto;
  bottom: auto;
  position: absolute !important;
  width: 370px !important;
  padding: 0 !important;
}

.Toastify__toast--default {
  padding: 20px !important;
  color: #fff !important;
  background-color: #20C476 !important;
  line-height: 20px;
}

.Toastify__close-button--default {
  font-size: 20px !important;
}


